import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GradesPage } from '../grades/grades';
/**
 * Generated class for the SelectSubjectPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-select-subject',
  templateUrl: 'select-subject.html',
})
export class SelectSubjectPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {

  }

  openGradesPage(value :any) {
    let sent = this.navParams.get('quarter');
    
    let data2 = {
      subject: value,
      quarter: sent
    }
    
  	this.navCtrl.push(GradesPage, data2);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectSubjectPage');
  }

}



