import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as firebase from 'firebase/app';
/**
 * Generated class for the SubjectAveragePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-subject-average',
  templateUrl: 'subject-average.html',
})
export class SubjectAveragePage {

  ap: Array<any> = [];
  ap1: Array<any> = [];
  ap2: Array<any> = [];
  ap3: Array<any> = [];
  eco: Array<any> = [];
  eco1: Array<any> = [];
  eco2: Array<any> = [];
  eco3: Array<any> = [];
  eng: Array<any> = [];
  eng1: Array<any> = [];
  eng2: Array<any> = [];
  eng3: Array<any> = [];
  esp: Array<any> = [];
  esp1: Array<any> = [];
  esp2: Array<any> = [];
  esp3: Array<any> = [];
  fil: Array<any> = [];
  fil1: Array<any> = [];
  fil2: Array<any> = [];
  fil3: Array<any> = [];
  mapeh: Array<any> = [];
  mapeh1: Array<any> = [];
  mapeh2: Array<any> = [];
  mapeh3: Array<any> = [];
  math: Array<any> = [];
  math1: Array<any> = [];
  math2: Array<any> = [];
  math3: Array<any> = [];
  sci: Array<any> = [];
  sci1: Array<any> = [];
  sci2: Array<any> = [];
  sci3: Array<any> = [];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    let sectionCode;
    let section;
    let myAp = this.ap;
    let myAp1 = this.ap1;
    let myAp2 = this.ap2;
    let myAp3 = this.ap3;
    let myEco = this.eco;
    let myEco1 = this.eco1;
    let myEco2 = this.eco2;
    let myEco3 = this.eco3;
    let myEng = this.eng;
    let myEng1 = this.eng1;
    let myEng2 = this.eng2;
    let myEng3 = this.eng3;
    let myesp = this.esp;
    let myesp1 = this.esp1;
    let myesp2 = this.esp2;
    let myesp3 = this.esp3;
    let myfil = this.fil;
    let myfil1 = this.fil1;
    let myfil2 = this.fil2;
    let myfil3 = this.fil3;
    let mymapeh = this.mapeh;
    let mymapeh1 = this.mapeh1;
    let mymapeh2 = this.mapeh2;
    let mymapeh3 = this.mapeh3;   
    let mymath = this.math;
    let mymath1 = this.math1;
    let mymath2 = this.math2;
    let mymath3 = this.math3; 
    let mysci = this.sci;
    let mysci1 = this.sci1;
    let mysci2 = this.sci2;
    let mysci3 = this.sci3; 

    var ref = firebase.database().ref("Students");
        ref.on('value', function(snap) {
          sectionCode = snap.child(firebase.auth().currentUser.uid + '/sectionCode').val();
          console.log(sectionCode); //

          var studRef = firebase.database().ref().child('Sections').orderByChild('sectionCode').equalTo(sectionCode);

          studRef.on('value', function(snapshot) {
          snapshot.forEach(userSnapshot => {
            var username = userSnapshot.val();
            console.log(username.secName);
            section = username.secName;

         var apRef1 = firebase.database().ref('/Araling Panlipunan/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var apRef2 = firebase.database().ref('/Araling Panlipunan/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var apRef3 = firebase.database().ref('/Araling Panlipunan/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var apRef4 = firebase.database().ref('/Araling Panlipunan/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var ecoRef1 = firebase.database().ref('/Economics/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var ecoRef2 = firebase.database().ref('/Economics/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var ecoRef3 = firebase.database().ref('/Economics/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var ecoRef4 = firebase.database().ref('/Economics/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var engRef1 = firebase.database().ref('/English/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var engRef2 = firebase.database().ref('/English/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var engRef3 = firebase.database().ref('/English/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var engRef4 = firebase.database().ref('/English/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var espRef1 = firebase.database().ref('/ESP/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var espRef2 = firebase.database().ref('/ESP/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var espRef3 = firebase.database().ref('/ESP/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var espRef4 = firebase.database().ref('/ESP/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var filRef1 = firebase.database().ref('/Filipino/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var filRef2 = firebase.database().ref('/Filipino/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var filRef3 = firebase.database().ref('/Filipino/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var filRef4 = firebase.database().ref('/Filipino/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var mapehRef1 = firebase.database().ref('/MAPEH/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var mapehRef2 = firebase.database().ref('/MAPEH/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var mapehRef3 = firebase.database().ref('/MAPEH/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var mapehRef4 = firebase.database().ref('/MAPEH/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var mathRef1 = firebase.database().ref('/Math/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var mathRef2 = firebase.database().ref('/Math/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var mathRef3 = firebase.database().ref('/Math/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var mathRef4 = firebase.database().ref('/Math/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         var sciRef1 = firebase.database().ref('/Science/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var sciRef2 = firebase.database().ref('/Science/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var sciRef3 = firebase.database().ref('/Science/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var sciRef4 = firebase.database().ref('/Science/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);

        apRef1.on('value', function(snapshot) {
          myAp.push({value:snapshot.child('QGRADE').val()});
        });
        apRef2.on('value', function(snapshot) {
          myAp1.push({value:snapshot.child('QGRADE').val()});
        });
        apRef3.on('value', function(snapshot) {
          myAp2.push({value:snapshot.child('QGRADE').val()});
        });
        apRef4.on('value', function(snapshot) {
          myAp3.push({value:snapshot.child('QGRADE').val()});
        });
        ecoRef1.on('value', function(snapshot) {
          myEco.push({value:snapshot.child('QGRADE').val()});
        });
        ecoRef2.on('value', function(snapshot) {
          myEco1.push({value:snapshot.child('QGRADE').val()});
        });
        ecoRef3.on('value', function(snapshot) {
          myEco2.push({value:snapshot.child('QGRADE').val()});
        });
        ecoRef4.on('value', function(snapshot) {
          myEco3.push({value:snapshot.child('QGRADE').val()});
        });
         engRef1.on('value', function(snapshot) {
          myEng.push({value:snapshot.child('QGRADE').val()});
        });
        engRef2.on('value', function(snapshot) {
          myEng1.push({value:snapshot.child('QGRADE').val()});
        });
        engRef3.on('value', function(snapshot) {
          myEng2.push({value:snapshot.child('QGRADE').val()});
        });
        engRef4.on('value', function(snapshot) {
          myEng3.push({value:snapshot.child('QGRADE').val()});
        });
         espRef1.on('value', function(snapshot) {
          myesp.push({value:snapshot.child('QGRADE').val()});
        });
        espRef2.on('value', function(snapshot) {
          myesp1.push({value:snapshot.child('QGRADE').val()});
        });
        espRef3.on('value', function(snapshot) {
          myesp2.push({value:snapshot.child('QGRADE').val()});
        });
        espRef4.on('value', function(snapshot) {
          myesp3.push({value:snapshot.child('QGRADE').val()});
        });
        filRef1.on('value', function(snapshot) {
          myfil.push({value:snapshot.child('QGRADE').val()});
        });
        filRef2.on('value', function(snapshot) {
          myfil1.push({value:snapshot.child('QGRADE').val()});
        });
        filRef3.on('value', function(snapshot) {
          myfil2.push({value:snapshot.child('QGRADE').val()});
        });
        filRef4.on('value', function(snapshot) {
          myfil3.push({value:snapshot.child('QGRADE').val()});
        });
        mapehRef1.on('value', function(snapshot) {
          mymapeh.push({value:snapshot.child('QGRADE').val()});
        });
        mapehRef2.on('value', function(snapshot) {
          mymapeh1.push({value:snapshot.child('QGRADE').val()});
        });
        mapehRef3.on('value', function(snapshot) {
          mymapeh2.push({value:snapshot.child('QGRADE').val()});
        });
        mapehRef4.on('value', function(snapshot) {
          mymapeh3.push({value:snapshot.child('QGRADE').val()});
        });
        mathRef1.on('value', function(snapshot) {
          mymath.push({value:snapshot.child('QGRADE').val()});
        });
        mathRef2.on('value', function(snapshot) {
          mymath1.push({value:snapshot.child('QGRADE').val()});
        });
        mathRef3.on('value', function(snapshot) {
          mymath2.push({value:snapshot.child('QGRADE').val()});
        });
        mathRef4.on('value', function(snapshot) {
          mymath3.push({value:snapshot.child('QGRADE').val()});
        });
        sciRef1.on('value', function(snapshot) {
          mysci.push({value:snapshot.child('QGRADE').val()});
        });
        sciRef2.on('value', function(snapshot) {
          mysci1.push({value:snapshot.child('QGRADE').val()});
        });
        sciRef3.on('value', function(snapshot) {
          mysci2.push({value:snapshot.child('QGRADE').val()});
        });
        sciRef4.on('value', function(snapshot) {
          mysci3.push({value:snapshot.child('QGRADE').val()});
        });
        return false;
      });

    });
   });
  }

}



